<template>
  <div>
    <b-alert
      :show="dismissCountDown"
      :variant="message.color"
      @dismissed="dismissCountDown = 0"
      @dismiss-count-down="countDownChanged"
    >
      {{message.text}}
    </b-alert>
  </div>
</template>

<script>

export default {
  name: 'NotesAlert',
  data() {
    return {
      dismissSecs: 2,
      dismissCountDown: 0,
      message: {
        color: '',
        text: ''
      }
    }
  },
  methods: {
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    },
    alert() {
      this.message.color = 'success'
      this.message.text = 'Probando alerta'
      this.showAlert()
    }
  },
  mounted() {
    this.$root.$on('addedNote', () => {
      this.message.color = 'success'
      this.message.text = 'Nota agregada correctamente'
      this.showAlert()
    })
    this.$root.$on('addedNoteError', error => {
      this.message.color = 'danger'
      this.message.text = error
      this.showAlert()
    })
    this.$root.$on('deletedNote', () => {
      this.message.color = 'success'
      this.message.text = 'Nota eliminada correctamente'
      this.showAlert()
    })
    this.$root.$on('modifiedNote', () => {
      this.message.color = 'success'
      this.message.text = 'Nota modificada correctamente'
      this.showAlert()
    })
  }
}
</script>
