<template>
  <form @submit.prevent="manageNote()">
    <h3>{{note._id ? 'Editar' : 'Agregar'}} nueva nota</h3>
    <b-form-input
      class="my-2"
      v-model="note.name"
      placeholder="Nombre"
    />
    <b-form-input
      class="my-2"
      v-model="note.description"
      placeholder="Descripción"
    />
    <div class="row mx-1">
      <b-button
        :class="note._id ? 'col-5' : 'col-12'"
        :disabled="loading"
        type="submit"
        :variant="note._id ? 'warning' : 'success'"
      >
        {{note._id ? 'Editar' : 'Agregar'}}
      </b-button>
      <div class="col-2">
      </div>
      <b-button
        v-if="note._id"
        class="col-5"
        :disabled="loading"
        type="submit"
        variant="secondary"
        @click="clearData()"
      >
        Cancelar
      </b-button>
    </div>
  </form>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  name: 'ManageNoteForm',
  data() {
    return {
      note: {
        name: '',
        description: ''
      },
      loading: false
    }
  },
  methods: {
    ...mapActions('Notes', ['addNote', 'editNote']),
    manageNote() {
      if(!!this.note._id) {
        this.editCurrentNote()
      } else {
        this.addNewNote()
      }
    },
    async addNewNote() {
      if(!this.note.name) {
        return
      }

      this.loading = true;
      await this.addNote(this.note)
        .then(() => {
          this.$root.$emit('addedNote')
          this.clearData()
        })
        .catch(error => {
          const { message } = error.response.data.error.errors.name
          this.$root.$emit('addedNoteError', message || 'Error de sistema')
        })
      this.loading = false;
      this.clearData()
    },
    async editCurrentNote() {
      if(!this.note.name) {
        return
      }

      this.loading = true;
      await this.editNote(this.note)
        .then(() => {
          this.$root.$emit('modifiedNote')
          this.clearData()
        })
        .catch(error => {
          const { message } = error.response?.data?.error?.errors?.name
          this.$root.$emit('modifiedNoteError', message || 'Error de sistema')
        })
      this.loading = false;
      this.clearData()
    },
    clearData() {
      this.note = {}
    }
  },
  mounted() {
    this.$root.$on('editNote', note => {
      this.note = note
    })
  }
}
</script>
