<template>
  <b-overlay :show="loading" rounded="sm" opacity="0.98">
    <table class="table">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">Nombre</th>
          <th scope="col">Descripción</th>
          <th scope="col">Acciones</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="note in notes" :key="note._id">
          <td>{{note._id}}</td>
          <td>{{note.name}}</td>
          <td>{{note.description}}</td>
          <td>
            <b-button @click="showEditForm(note)" variant="warning">
              Editar
            </b-button>
            <b-button @click="removeNote(note._id)" class="ms-1" variant="danger">
              Eliminar
            </b-button>
          </td>
        </tr>
      </tbody>
    </table>
  </b-overlay>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {
  name: 'NotesTable',
  data() {
    return {
      loading: true
    }
  },
  async created() {
    await this.listNotes()
    this.loading = false
  },
  methods: {
    ...mapActions('Notes', ['listNotes', 'deleteNote']),
    showEditForm(note) {
      const noteToEdit = {...note}
      this.$root.$emit('editNote', noteToEdit)
    },
    async removeNote(noteId) {
      this.loading = true;
      await this.deleteNote(noteId)
        .then(() => {
          this.$root.$emit('deletedNote')
        })
        .catch(error => {
          const { message } = error.response.data.error.errors.name
          this.$root.$emit('deletedNoteError', message || 'Error de sistema')
        })
      this.loading = false;
    }
  },
  computed: {
    ...mapGetters('Notes', ['notes'])
  }
}
</script>
